import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ServiceService } from 'src/app/servicios/service.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-formulario',
  templateUrl: './formulario.component.html',
  styleUrls: ['./formulario.component.css'],
})
export class FormularioComponent implements OnInit {
  public formulario: any;

  constructor(
    private _formBuilder: FormBuilder,
    private serv: ServiceService
  ) {}

  ngOnInit(): void {
    this.formulario = this._formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      message: ['', Validators.required],
    });
  }

  submit() {
    if (this.formulario.valid) {
      let email = {
        receptor: 'irving.arzateug@hotmail.com',
        asunto: this.formulario.value.name + this.formulario.value.email,
        mensaje: this.formulario.value.message,
      };

      this.serv.savedata(email).subscribe((data) => {
        Swal.fire({
          icon: 'success',
          text: 'Sus datos se enviaron con exito!',
        });
      });
    } else {
      Swal.fire({
        icon: 'error',
        text: 'Por favor llene los datos requeridos!',
      });
    }
  }
}
